.mainContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: rgba(255, 255, 255, 0.85);

  .projectsContainer {
    display: flex;
    flex-wrap: wrap;
    align-content: space-between;
    justify-content: center;
    height: 90%;
    width: 70%;

    .projects {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      width: 100%;
      height: 80%;
      @media screen and (max-width: 917px) {
        justify-content: center;
      }
    }
  }
}
